import React, {Component} from 'react';
import {Card, Form, Select, Slider, Row, Col, Tooltip, Spin, Statistic, Empty, Space, Result} from "antd";
import {QuestionCircleOutlined } from "@ant-design/icons";

import './border.css'

class Local extends Component {
    state = {mode: 'stupid', size: 3, loading: false, result: null, t: new Date().getTime()}

    formRef = React.createRef()

    onModeChange(mode) {
        this.setState({mode})
    }

    onSizeChange(size) {
        this.setState({size})
    }

    startComputeCreator() {
        const that = this;
        return () => {
            that.setState({loading: true})
            return that.formRef?.current?.getFieldsValue(true)
        }
    }

    finishComputeCreator() {
        const that = this;
        return result => {
            that.setState({loading: false, result, t: new Date().getTime()})
        }
    }

    render() {
        return (
            <div className="content">
                <Card title="" style={{width: '100%'}} size="small">
                    <Form ref={this.formRef} layout="inline" initialValues={{
                        mode: 'stupid',
                        size: 3,
                        en: 10,
                        pn: 6
                    }}>
                        <Form.Item label="" name="mode">
                            <Select
                                style={{width: 90}}
                                onChange={v => this.onModeChange(v)}
                                options={[
                                    {
                                        value: 'stupid',
                                        label: '',
                                    },
                                    {
                                        value: 'easy',
                                        label: '',
                                    },
                                    {
                                        value: 'normal',
                                        label: '',
                                    },
                                    {
                                        value: 'custom',
                                        label: '',
                                    },
                                ]}
                            />
                        </Form.Item>
                        <Form.Item label="" name="size">
                            <Select
                                disabled={this.state.mode !== 'custom'}
                                style={{width: 80}}
                                onChange={v => this.onSizeChange(v)}
                                options={[
                                    {
                                        value: 3,
                                        label: '3x3',
                                    },
                                    {
                                        value: 4,
                                        label: '4x4',
                                    },
                                    {
                                        value: 5,
                                        label: '5x5',
                                    },
                                    {
                                        value: 6,
                                        label: '6x6',
                                    },
                                ]}
                            />
                        </Form.Item>
                        <Form.Item label="" name="pn">
                            <Slider
                                min={this.state.size}
                                max={this.state.size * this.state.size}
                                style={{width: 120}}
                                disabled={this.state.mode !== 'custom'}
                            />
                        </Form.Item>
                        <Form.Item label="" name="en">
                            <Slider min={4} max={15} style={{width: 120}} marks={{
                                4: '',
                                15: ''
                            }} tooltip={{formatter: null}} disabled={this.state.mode !== 'custom'}/>
                        </Form.Item>
                    </Form>
                </Card>
                <Row style={{paddingTop: 5, paddingBottom: 5}} gutter={5}>
                    <Col span={15}>
                        <Card title="" size="small" style={{height: 631, overflow: "hidden"}} >
                            <Spin spinning={this.state.loading} delay={500} size="large">
                                {
                                    this.state.result ?
                                        <div style={{width: '100%', height: 570, margin: 10}}>
                                            <Result
                                                status={this.state.result.ok? "success": "warning"}
                                                title={this.state.result.ok? "": ""}
                                            />
                                            <Row>
                                                <Col span={12}>
                                                    <Space direction="vertical" size="middle">
                                                        <Statistic title="" value={this.state.result.time} />
                                                        <Statistic title="" value={this.state.result.np} />
                                                        <Statistic title="" value={this.state.result.nr} />
                                                    </Space>
                                                </Col>
                                                <Col span={12}>
                                                    <Space direction="vertical" size="middle">
                                                        <Statistic
                                                            title=""
                                                            value={this.state.result.co ? "": ""}
                                                        />
                                                        <Statistic title="" value={this.state.result.ep} />
                                                        <Statistic title="" value={this.state.result.er} />
                                                    </Space>
                                                </Col>
                                            </Row>
                                        </div> :
                                        <Empty style={{marginTop: 100}} image={Empty.PRESENTED_IMAGE_SIMPLE} />
                                }
                            </Spin>
                        </Card>
                    </Col>
                    <Col span={9}>
                        <Card title="" size="small">
                            <div style={{width: "100%", height: 250, overflow: "hidden"}}>
                                {
                                    this.state.result ?
                                        <img src={`/images/question.png?t=${this.state.t}`} alt="question" style={{
                                            width: 250,
                                            height: 250,
                                            alignSelf: 'center'
                                        }}/> :
                                        <Empty image={Empty.PRESENTED_IMAGE_SIMPLE} />
                                }
                            </div>
                        </Card>
                        <Card title="" size="small" style={{marginTop: 5}} extra={
                            <Tooltip placement="bottomRight" title={
                                <div>
                                    <br />
                                    <br />
                                    <br />
                                    <br />
                                    
                                </div>
                            }>
                                <QuestionCircleOutlined />
                            </Tooltip>
                        }>
                            <div style={{width: "100%", height: 250, overflow: "hidden"}}>
                                {
                                    this.state.result ?
                                        <img src={`/images/result.png?t=${this.state.t}`} alt="result" style={{
                                            width: 250,
                                            height: 250,
                                            alignSelf: 'center'
                                        }}/> :
                                        <Empty image={Empty.PRESENTED_IMAGE_SIMPLE} />
                                }
                            </div>
                        </Card>
                    </Col>
                </Row>
            </div>
        );
    }
}

export default Local;